document.addEventListener("DOMContentLoaded", function () {
  iniciarApp();
});

var NetlifyProjects = ["music-festival"];
var HerokuProjects = ["frontend-store", "bienesraices"];

function iniciarApp() {
  proyectos();
  errores();
}

function proyectos() {
  const galeria = document.querySelector(".galeria-imagenes");

  for (let i = 0; i <= NetlifyProjects.length - 1; i++) {
    const imagen = document.createElement("li");
    imagen.innerHTML = `
        <picture>
          <a href="https://${NetlifyProjects[i]}-rafael-perez.netlify.app/">
              <source srcset="build/img/NetlifyProjects/${NetlifyProjects[i]}.avif" type="image/avif">
              <source srcset="build/img/NetlifyProjects/${NetlifyProjects[i]}.webp" type="image/webp">
              <img loading="lazy" width="200" height="300" src="build/img/NetlifyProjects/${NetlifyProjects[i]}.png" alt="imagen galeria">
          </a>
        </picture>
        `;
    galeria.appendChild(imagen);
  }

  for (let i = 0; i <= HerokuProjects.length - 1; i++) {
    const imagen = document.createElement("li");
    imagen.innerHTML = `
        <picture>
          <a href="https://${HerokuProjects[i]}-rafael-perez.herokuapp.com/">
              <source srcset="build/img/HerokuProjects/${HerokuProjects[i]}.avif" type="image/avif">
              <source srcset="build/img/HerokuProjects/${HerokuProjects[i]}.webp" type="image/webp">
              <img loading="lazy" width="200" height="300" src="build/img/HerokuProjects/${HerokuProjects[i]}.png" alt="imagen galeria">
          </a>
        </picture>
        `;
    galeria.appendChild(imagen);
  }
}

function errores() {
  const button = document.querySelector(".boton");
  button.addEventListener("click",function(e){
    e.preventDefault();
    console.log("pressed");
  })
}